.center-cropped{
    overflow:hidden;
    position:relative;
    padding-bottom: 60%;   
}

.image{
    position: absolute;
    max-width: 100%;
    min-height: 100%;
    /* max-height: 100%; */
    top: 50%;
    left: 50%;
    -webkit-transform: translateX(-50%) translateY(-50%);
        -ms-transform: translateX(-50%) translateY(-50%);
            transform: translateX(-50%) translateY(-50%);
    
}


